<script>
  import '../assets/js/china.js'//
  import map1 from '../components/index/map.vue'//
  import editor from '../components/index/editor.vue'//
  import radar from '../components/index/radar.vue'//
  import linear from '../components/index/linear.vue'//

  import countTo from 'vue-count-to';
  import BScroll from 'better-scroll';

  import {getData,getMachineSurvey,getMonitoring,getAttackTop20} from '../api/index/situation.js';
export default {
  data(){
    return{
      virus: {},
      machine: {},
      surveillance: {},
      attack: [],
      timer: 1,
      h: 0,
      scroll3: {},
      sumh: 0
    }
  },
  methods: {
    play(){
      let sumh = -this.attack.length*25;
      this.sumh = sumh;
      if(-sumh < 185){
        return;
      }
      this.timer = setInterval(() => {
        this.h--;
        if(this.h<= sumh){
          this.h = 0;
        }
        this.scroll3.scrollTo(0,this.h);
      },1000/20);
    },
    clear(){
      clearInterval(this.timer)
    }
  },
  mounted(){
    getAttackTop20().then(res => {//
      console.log('',res);
      this.attack = res.result;
      setTimeout(() => {
        this.scroll3 = new BScroll(this.$refs.content)
        this.play()

      },500);
    })
    getMonitoring().then(res => {//
      //console.log('',res);
      this.surveillance = res.result;
    })
    getMachineSurvey().then(res => {//
      //console.log('',res);
      this.machine = res.result;
    })
    getData().then(res => {//
      this.virus = res.result;
    })
    setInterval(() => {
      getData().then(res => {//
        this.virus = res.result;
      })
    },10000);
  },
  components: {
    map1,
    editor,
    radar,
    linear,
    countTo
  },
}
</script>

